
@charset "UTF-8";
header {
  height: 280px;
  background: url("../../src/images/top.jpg") no-repeat;
  background-size: 100%;
  overflow: hidden;
}
header h2 {
    font-size: 36px;
    font-family: '微软雅黑,幼圆,微软雅黑,微软雅黑';
    margin-top: 180px;
}
header .nav {
    margin-left: 50px;
    margin-top: 10px;
}
header .nav li {
      float: left;
      margin-right: 30px;
      font-size: 14px;
      color: #555;
}
header .nav li a {
        display: inline-block;
        color: #555;
        padding: 5px 10px;
}



